@import "./styles/quiz.css";
@import "./styles/score.css";
@import "./styles/footer.css";

:root {
    background-color: #7aabd659; // #ecf5ff;
    font-size: 62.5%;
}

@font-face {
    font-family: 'Comic Sans MS';
    src: url("./fonts/comic-sans-ms/COMIC.TTF");
}

* {
    box-sizing: border-box;
    font-family: 'Comic Sans MS', 'Marker Felt', sans-serif; //Verdana, Geneva, Tahoma, sans-serif;
    margin: 0;
    padding: 0;
    //color: #333;
}

h1,
h2,
h3,
h4 {
    margin-bottom: 1rem;
}

h1 {
    font-size: 5.4rem;
    color: #00506b;
    margin-bottom: 5rem;
}

h1>span {
    font-size: 2.4rem;
    font-weight: 500;
}

h2 {
    font-size: 3.2rem;
    margin-bottom: 4rem;
    font-weight: 700;
    color: #001820;
    text-align: center;
}

h3 {
    font-size: 2.8rem;
    font-weight: 500;
    justify-content: center;
    text-align: center;
    margin-bottom: 5rem;
}

/* UTILITIES */

.container {
    width: 100vw;
    height: 93vh;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 85rem;
    margin: 0 auto;
    padding: 0rem;
}

.container>* {
    width: 100%;
}

.flex-column {
    display: flex;
    flex-direction: column;
}

#category>div {
    display: flex;
    width: 100%;
}

#category>div>a {
    margin: 15px;
}

.flex-center {
    justify-content: center;
    align-items: center;
}

.justify-center {
    justify-content: center;
}

.text-center {
    text-align: center;
}

.hidden {
    display: none;
}

/* BUTTONS */
.btn {
    font-size: 1.8rem;
    padding: 1rem 0;
    width: 20rem;
    text-align: center;
    border: 0.1rem solid #00506b;
    margin-bottom: 1rem;
    text-decoration: none;
    color: #00506b;
    background-color: white;
}

.btn:hover {
    cursor: pointer;
    box-shadow: 0 0.6rem 2.4rem 0 rgba(86, 185, 235, 0.5);
    transform: translateY(-0.1rem);
    transition: transform 150ms;
}

.btn[disabled]:hover {
    cursor: not-allowed;
    box-shadow: none;
    transform: none;
}

/* FORMS */
form {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

input {
    margin-bottom: 1rem;
    width: 20rem;
    padding: 1.5rem;
    font-size: 1.8rem;
    border: none;
    box-shadow: 0 0.1rem 1.4rem 0 rgba(86, 185, 235, 0.5);
}

input::placeholder {
    color: #aaa;
}